<template>
  <div class="inputs">
    <AccountInformation class="input" id="account"
      loadText="Load Characters"
      :errorMessage="errorMessage"
      @load-account="getCharacters"
      @api="setApi" />

    <div class="input" id="characters">
      <label for="characterList" class="text">Characters</label>
      <select v-model="character" @change="characterChanged" id="characterList">
        <option disabled>--Select a character--</option>
        <option v-for="character in characters" :value="character">
          {{ character.name }} ({{ character.league }})
        </option>
      </select>
    </div>

    <LegendDisplay id="legend" />
    
    <div class="input big" id="locations">
      <fieldset>
        <legend>Locations to include</legend>
        <div id="stashes" class="stashes">
          <div class="stash" v-show="character.name">
            <input type="checkbox" class="stash-checkbox" id="inventory" v-model="stashes" :value="character.name + '-inv'">
            <label for="inventory" class="checkbox" checked>Character Inventory</label>
          </div>
          <div v-for="tab in stashTabs" :key="tab.id" class="stash">
            <input type="checkbox" class="stash-checkbox" :id="tab.n" v-model="stashes" :value="tab.i">
            <label :for="tab.n" class="checkbox">{{ tab.n }}</label>
          </div>
        </div>
      </fieldset>
      <button type="button" @click="getItems">Load Maps</button>
      <div class="note" id="notes">
          Note: Clicking 'Load Maps' will overwrite the checkboxes in the 'Have' column.<br />
          <b>Also Note</b>: Premium 'Maps' stash tabs currently don't return any information.  GGG plz.
      </div>
    </div>
  </div>
</template>

<script>
import Api from '../APIService.js'
import LegendDisplay from './LegendDisplay'
import AccountInformation from './AccountInformation'

export default {
  name: 'CharacterSelection',
  components: {
    LegendDisplay,
    AccountInformation,
  },
  data () {
    return {
      characters: [],
      character: '--Select a character--',
      stashes: [],
      stashTabs: [],
      foundMaps: [],
      api: null,
      errorMessage: ""
    }
  },
  methods: {
    characterChanged () {
      while (this.stashes.find(s => s.toString().includes('-inv'))) {
        const invIndex = this.stashes.findIndex(s => s.toString().includes('-inv'));

        if (invIndex > -1) {
          this.stashes.splice(invIndex, 1);
        }
      }

      localStorage.setItem('character', JSON.stringify(this.character));
      localStorage.setItem('stashes', JSON.stringify(this.stashes));

      this.api.getStashes(this.character.league)
      .then(tabs => {
        const displayTabs = tabs.filter(t => {
          if (t.type !== "CurrencyStash" &&
            t.type !== "FragmentStash" &&
            t.type !== "DivinationCardStash" &&
            t.type !== "EssenceStash") {
              
              return true;
            }
        });

        this.stashTabs = displayTabs;
        localStorage.setItem('stashTabs', JSON.stringify(this.stashTabs));
      })
      .catch(error => {
        // handle error
        console.log(error.response);
      });
    },

    addMaps (result) {          
      if (result.data) {
        let items;
        
        // treat response from inventory differently
        // inventory call responds with information about character
        // stash calls don't
        if (result.data.items) {
          items = result.data.items;
        }
        else {
          items = result.data;
        }

        const maps = items.filter(i => i.category.maps && i.typeLine.includes("Map"));
        this.foundMaps = this.foundMaps.concat(maps);
      }
    },

    getCharacters (data) {
      this.api = new Api(data.accountName, data.sessionId);
      this.stashes = [];
      this.stashTabs = [];
      this.character = '--Select a character--'
      this.characters = [];
      localStorage.removeItem('stashes');
      localStorage.removeItem('stashTabs');
      this.errorMessage = "";

      this.api.getCharacters()
      .then(characters => {
        this.characters = characters;
        this.character = characters.find(c => c.lastActive);

        localStorage.setItem('sessionId', data.sessionId);
        localStorage.setItem('accountName', data.accountName);
        localStorage.setItem('characters', JSON.stringify(this.characters));
        localStorage.setItem('character', JSON.stringify(this.character));
      })
      .then(() => this.api.getStashes(this.character.league))
      .then(tabs => {
        const displayTabs = tabs.filter(t => {
          if (t.type !== "CurrencyStash" &&
            t.type !== "FragmentStash" &&
            t.type !== "DivinationCardStash" &&
            t.type !== "EssenceStash") {
              
              return true;
            }
        });

        this.stashTabs = displayTabs;
        localStorage.setItem('stashTabs', JSON.stringify(this.stashTabs));
      })
      .catch(error => {
        const errorUrl = error.request.responseURL;

        if (errorUrl.includes('characters')) {
          this.errorMessage = 'Error retrieving characters.  Confirm your POESESSID and account name are correct.';

          localStorage.setItem('characters', JSON.stringify(this.characters));
          localStorage.setItem('character', JSON.stringify(this.character));
          localStorage.setItem('stashTabs', JSON.stringify(this.stashTabs));
        }
        else if (errorUrl.includes('stashes')) {
          this.errorMessage = 'Error retrieving stashes.  Confirm your account name is correct.';          
        }
      });
    },

    getItems () {
      this.foundMaps = [];
      this.errorMessage = "";

      this.api.getItems(this.character, this.stashes)
      .then(results => {
        results.forEach(r => this.addMaps(r));
        this.$emit('items-loaded', this.foundMaps);
        localStorage.setItem('stashes', JSON.stringify(this.stashes));
      })
      .catch(error => {
        // handle error
        console.log(error);        
      });
    },

    setApi (api) {
      this.api = api;
    }
  },
  created () {
    const characters = JSON.parse(localStorage.getItem('characters'));
    const character = JSON.parse(localStorage.getItem('character'));
    const stashTabs = JSON.parse(localStorage.getItem('stashTabs'));
    const stashes = JSON.parse(localStorage.getItem('stashes'));

    if (characters) {
      this.characters = characters;
    }

    if (character) {
      this.character = character;
    }

    if (stashTabs) {
      this.stashTabs = stashTabs;
    }

    if (stashes) {
      this.stashes = stashes;
    }
  }
}
</script>

<style>
label.text {
  display: block;
  padding-top: 5px;
}

label.checkbox {
  padding-left: 2px;
}

input[type="text"] {
  width: 200px;
}

button {
  margin-top: 5px;
  float: right;
}

#stashes {
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: repeat(8, 20px);
  grid-template-columns: repeat(auto-fill, min-content);
}

#locations {
  grid-area: locations;
}

#account {
  grid-area: account;
}

#legend {
  grid-area: legend;
  align-self: top;
  justify-self: left;
}

#characters {
  grid-area: characters;
  justify-self: left;
}

.stash {
  margin: 2px;
}

div.inputs {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-rows: 93px 1fr;
  grid-template-areas: 
    "account characters locations"
    "account legend locations";
  justify-items: center;
  width: 100%;
}

div.input {
  padding: 10px;
}

div.note {
  margin-top: 5px;
  margin-left: 20px;
}

a {
  font-size: 8pt;
}

input[type=checkbox].stash-checkbox {
  transform: scale(1.25);
}


</style>
